.card {
  color: #fff;

  &__image {
    width: 280px;
    margin-bottom: 32px;

    @include onDesktop {
      width: 100%;
      margin-bottom: 32px;
    }
  }

  &__name {
    width: 100%;
    font-weight: 700;
    font-size: 24px;
    line-height: 140%;
    margin-bottom: 8px;

    &--last {
      width: 278px;

      @include onDesktop {
        width: 672px;
        margin-bottom: 48px;
      }
    }
  }

  &__text {
    width: 100%;
    font-weight: 400;
    font-size: 16px;
    line-height: 140%;
    margin-bottom: 16px;

    &--last {
      width: 278px;
      margin-bottom: 40px;

      @include onDesktop {
        width: 672px;
        margin-bottom: 48px;
      }
    }
  }

  &__price {
    font-weight: 700;
    font-size: 24px;
    line-height: 140%;
  }
}
